import { Component, ElementRef, OnDestroy, OnInit, ViewChild } from '@angular/core';
import { NavigationStart, Router } from "@angular/router";
import { fromEvent, Subject } from "rxjs";
import { filter, takeUntil } from "rxjs/operators";

import MENU from 'src/app/configuration/menu/menu';
import contact from "../../../../../../configuration/contact/contact";
import { TaxonomyRecordInterface } from "../../../../../../taxonomy/interface/taxonomy-record.interface";
import { TaxonomyTypeInterface } from "../../../../../../taxonomy/interface/taxonomy-type.interface";
import { SearchService } from "../../../../../../taxonomy/service/search.service";

@Component({
  selector: 'app-mobile-heading',
  templateUrl: './heading.component.html',
  styleUrls: ['./heading.component.less']
})
export class HeadingComponent implements OnInit, OnDestroy {
  contactList = contact;

  destroy$ = new Subject();

  menu = MENU;

  searchActive: boolean;
  searchQuery: string;

  contactsOpen: boolean;
  contactsClick: number = 0;

  searchTypes: TaxonomyTypeInterface[];
  results: { [_: string]: TaxonomyRecordInterface[] };

  @ViewChild('contacts', {static: true}) contactsRef: ElementRef<HTMLDivElement>;

  constructor(private searchService: SearchService,
              private router: Router) {
  }

  ngOnInit() {
    this.router.events
      .pipe(filter(e => e instanceof NavigationStart), takeUntil(this.destroy$))
      .subscribe(() => this.disableSearch());

    fromEvent(document.body, 'click')
      .pipe(takeUntil(this.destroy$))
      .subscribe(() => {

        if (this.contactsOpen) {
          if (this.contactsClick) {
            this.contactsClick = Math.max(0, this.contactsClick - 1);
          } else {
            setTimeout(() => {
              if (!this.contactsClick) {
                this.contactsOpen = false;
              }
            })
          }
        }
      });

    fromEvent(this.contactsRef.nativeElement, 'click')
      .pipe(takeUntil(this.destroy$))
      .subscribe(() => {
        this.contactsClick++;
      })
  }

  openContacts() {
    if (!this.contactsOpen) {
      this.contactsOpen = true;
      this.contactsClick++;
    }
  }

  closeContacts() {
    this.contactsOpen = false;
    this.contactsClick = 0;
  }

  disableSearch() {
    this.searchQuery = '';
    this.searchActive = false;

    this.search(null);
  }

  search(s: string) {
    if (!s || s.length < 3) {
      this.results = {};
      this.searchTypes = null;
    } else {
      this.results = this.searchService.searchByType(s);
      this.searchTypes = this.results ? Object.keys(this.results).map(t => this.results[t][0].type) : null;
    }
  }

  ngOnDestroy() {
    this.destroy$.next();
    this.destroy$.complete();
  }

}
