import { AfterViewInit, Component, ElementRef, OnInit, ViewChild } from '@angular/core';
import { ActivatedRoute } from "@angular/router";

import TEACHERS from 'src/app/configuration/teacher/teachers';
import { isNumber, isString } from "util";

@Component({
  selector: 'app-employee-page',
  templateUrl: './employee-page.component.html',
  styleUrls: ['./employee-page.component.less']
})
export class EmployeePageComponent implements AfterViewInit {
  readonly teachers = TEACHERS;

  @ViewChild('teacherList', { static: true }) teacherListRef: ElementRef<HTMLDivElement>;

  constructor(private route: ActivatedRoute) { }

  ngAfterViewInit() {
    this.route.params.subscribe(({id}) => {
      if(isString(id)) {
        const num = Math.min(TEACHERS.length - 1, Math.max(0, parseInt(id, 10)));
        document.body.scrollTop =
          this.teacherListRef.nativeElement.children[num].getBoundingClientRect().top +
          document.body.scrollTop;
      }
    });
  }

}
