import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { GeneralLayoutComponent } from '../../shared/layout/general-layout/general-layout.component';
import { GeneralLayoutModule } from '../../shared/layout/general-layout/general-layout.module';
import { EmployeePageComponent } from "../../shared/page/employee-page/employee-page.component";
import { EmployeePageModule } from "../../shared/page/employee-page/employee-page.module";
import { HealingPageComponent } from "../../shared/page/healing-page/healing-page.component";
import { HealingPageModule } from "../../shared/page/healing-page/healing-page.module";
import { LandingPageComponent } from '../../shared/page/landing-page/landing-page.component';
import { LandingPageModule } from '../../shared/page/landing-page/landing-page.module';
import { LearningPageComponent } from "../../shared/page/learning-page/learning-page.component";
import { LearningPageModule } from "../../shared/page/learning-page/learning-page.module";
import { SciencePageComponent } from "../../shared/page/science-page/science-page.component";
import { SciencePageModule } from "../../shared/page/science-page/science-page.module";

const routes: Routes = [
  {
    path: '',
    component: GeneralLayoutComponent,
    children: [
      {
        path: '',
        component: LandingPageComponent
      },
      {
        path: 'employee',
        children: [
          {
            path: '',
            component: EmployeePageComponent
          },
          {
            path: ':id',
            component: EmployeePageComponent
          }
        ]
      },
      {
        path: 'science-research',
        component: SciencePageComponent
      },
      {
        path: 'learning-research',
        component: LearningPageComponent
      },
      {
        path: 'healing-research',
        component: HealingPageComponent
      },
    ]
  }
];

@NgModule({
  imports: [
    GeneralLayoutModule,
    LandingPageModule,
    EmployeePageModule,
    SciencePageModule,
    LearningPageModule,
    HealingPageModule,
    RouterModule.forChild(routes)
  ],
  exports: [RouterModule]
})
export class BaseModule {
}
