import { Component, ElementRef, OnInit, ViewChild } from '@angular/core';
import { interval, Subject } from "rxjs";
import { takeUntil } from "rxjs/operators";

import { DeviceDetectorService } from '../../service/device-detector.service';
import TEACHERS from 'src/app/configuration/teacher/teachers';

@Component({
  selector: 'app-landing-page',
  templateUrl: './landing-page.component.html',
  styleUrls: ['./landing-page.component.less']
})
export class LandingPageComponent implements OnInit {
  teachers = TEACHERS;

  scrolling = false;

  slide: number = 0;

  @ViewChild('scrollBox', { static: false }) scrollBox: ElementRef<HTMLElement>;

  constructor(public device: DeviceDetectorService) { }

  ngOnInit() {
  }

  scroll(reverse: boolean) {
    if (this.scrolling) {
      return;
    }

    let c = s => (this.scrollBox.nativeElement.children[s] as any).offsetLeft;

    let scrollFrom = c(this.slide);
    let scrollTo = 0;

    if (reverse) {
      this.slide = Math.max(0, this.slide - 1);
    } else {
      this.slide = Math.min(this.scrollBox.nativeElement.children.length - 1, this.slide + 1);
    }

    scrollTo = c(this.slide);

    const scrollWidth = this.scrollBox.nativeElement.scrollWidth - this.scrollBox.nativeElement.offsetWidth;

    if (scrollTo > scrollWidth) {
      scrollTo = scrollWidth;
      this.slide --;
    }

    if (scrollTo !== scrollFrom) {

      this.scrolling = true;

      const scrollTime = 100;
      const scrollInterval = 5;

      const scrollSteps = scrollTime / scrollInterval;
      const scrollPerStep = (scrollTo - scrollFrom) / scrollSteps;

      let scrollStep = 0;

      const stop$ = new Subject<void>();

      interval(scrollInterval)
        .pipe(takeUntil(stop$))
        .subscribe(() => {
          if (++scrollStep === scrollSteps) {
            this.scrollBox.nativeElement.scrollLeft = scrollTo;

            stop$.next();
            stop$.complete();

            this.scrolling = false;
          } else {
            this.scrollBox.nativeElement.scrollLeft = scrollFrom + (scrollPerStep * scrollStep);
          }
        });
    }
  }
}
